@use "../../foundation/mixins" as *;
@use "../../foundation/variables" as *;

// 生徒さんたちの声
.p-voice {
  padding: 6.5rem 0 6rem;
  &__wrapper {
    margin-top: 3.6rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3.5rem;
    @include sp {
      display: initial;
    }
  }
  &__image {
    width: 27rem;
  }
  &__name {
    margin-top: 1.7rem;
    text-align: center;
    font-size: 2.2rem;
    font-weight: bold;
  }
  &__comment {
    margin-top: 1rem;
    text-align: center;
    font-size: 1.8rem;
    line-height: calc(30 / 18);
    font-size: medium;
  }
}
